<template>
  <div class="widgetContainer bg-white widgetContainer--scrollable pay">
    <div class="widgetContainer__header-fixed" v-if="paymentStatus == ''">
      <div class="navigationHeader">
        <div class="navigationHeader__inner">
          <span class="icon-Arrow-big-left cancel" @click="cancel" />
          <p class="title">
            {{ $t('payment_domesticwire_title') }}
          </p>
          <span class="el-icon-close" @click="drawerClose" />   
        </div>
      </div>
    </div>
    <div class="widgetContainer__body">
      <div class="pay__details">
        <el-form
          class="wise-form"
          :model="contactDetails.wire.domestic"
          :rules="formRules">
          <el-form-item
            class="is-no-asterisk"
            prop="accountNumber"
            :label="$t('contact_Account_AccNo')">
            <AccountNumber
              v-model="contactDetails.wire.domestic.accountNumber" />
          </el-form-item>

          <el-form-item
            class="is-no-asterisk"
            prop="routingNumber"
            :label="$t('acc_detail_rout_title')">
            <RouteNumber v-model="contactDetails.wire.domestic.routingNumber" />
          </el-form-item>

          <el-form-item
            class="is-no-asterisk"
            prop="accountType"
            :label="$t('contact_Account_Type')">
            <el-select v-model="contactDetails.wire.domestic.accountType">
              <el-option :label="$t('personalSavings')" value="personalSavings">
                {{ $t('personalSavings') }}
              </el-option>
              <el-option :label="$t('personalChecking')" value="personalChecking">
                {{ $t('personalChecking') }}
              </el-option>
              <el-option :label="$t('businessSavings')" value="businessSavings">
                {{ $t('businessSavings') }}
              </el-option>
              <el-option :label="$t('businessChecking')" value="businessChecking">
                {{ $t('businessChecking') }}
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item
            class="is-no-asterisk"
            prop="bankName"
            :label="$t('contact_Account_Bank')">
            <el-input v-model="contactDetails.wire.domestic.bankName" />
          </el-form-item>

          <el-form-item
            class="is-no-asterisk"
            prop="amount"
            :label="$t('pay_section_amount')">
            <Amount :precision="2" v-model="formData.amount" :masked="true" />
          </el-form-item>

          <el-form-item
            class="is-no-asterisk"
            prop="description"
            :label="$t('pay_row_purpose')">
            <el-input
              placeholder="Purpose of the payment"
              v-model="formData.description" />
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="el-drawer__footer">
      <el-button
        type="primary"
        :disabled="!isFormValid"
        @click="next()"
        class="width-100 el-button__brand brand">
        {{ $t('pay') }}
      </el-button>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';
import Amount from '@/components/Amount';
import AccountNumber from '@/components/AccountNumber';
import RouteNumber from '@/components/RouteNumber';
import { payToContact } from '../utils/pay';

export default {
  components: {
    Amount,
    AccountNumber,
    RouteNumber
  },
  created() {
    if (!this.getPaymentType) this.$router.push('/pay/paymentType');

    if (!this.getPayDetails || !this.getPayDetails.address)
      this.$router.push('/pay/domestic-wire/address');
    else this.contactDetails.wire.domestic.address = this.getPayDetails.address;

    if (
      this.getSelectedContactData &&
      this.getSelectedContactData.wire &&
      this.getSelectedContactData.wire.domestic.accountType
    ) {
      this.contactDetails.wire = { ...this.getSelectedContactData.wire };
    }
  },
  computed: {
    ...mapGetters('pay', ['getPaymentType', 'getPayDetails']),
    ...mapGetters('account', ['getSelectedAccount']),
    ...mapGetters('contact', ['getSelectedContactData']),
    isFormValid() {
      if (
        this.formData.amount !== '' &&
        this.formData.description !== '' &&
        this.contactDetails.wire.domestic.accountNumber &&
        this.contactDetails.wire.domestic.accountNumber !== '' &&
        this.contactDetails.wire.domestic.routingNumber !== '' &&
        this.contactDetails.wire.domestic.accountType !== '' &&
        this.contactDetails.wire.domestic.bankName !== '' &&
        this.contactDetails.wire.domestic.accountType !== '' &&
        this.contactDetails.wire.domestic.accountNumber.length >= 6 &&
        this.contactDetails.wire.domestic.accountNumber.length <= 17 &&
        this.contactDetails.wire.domestic.routingNumber.length === 9
      ) {
        const amount = parseFloat('' + this.formData.amount);
        return amount >= 0.05;
      }
      return false;
    }
  },
  data() {
    return {
      formData: {
        amount: '0',
        description: ''
      },
      contactChange: false,
      contactDetails: {
        wire: {
          domestic: {
            accountNumber: '',
            accountType: '',
            bankName: '',
            routingNumber: ''
          }
        }
      },
      formRules: {
        accountNumber: [
          {
            required: true,
            message: this.$t('accountNumberRequired'),
            trigger: 'blur'
          },
          {
            pattern: /^\d{6,17}$/,
            message: this.$t('accountNumberNotValid'),
            trigger: 'blur'
          }
        ],
        routingNumber: [
          {
            required: true,
            message: this.$t('routingNumberRequired'),
            trigger: 'blur'
          },
          {
            pattern: /^\d{9}$/,
            message: this.$t('routingNumberNotValid'),
            trigger: 'blur'
          }
        ],
        accountType: [
          {
            required: true,
            message: this.$t('accountTypeRequired'),
            trigger: 'blur'
          }
        ],
        bankName: [
          {
            required: true,
            message: this.$t('bankNameRequired'),
            trigger: 'blur'
          }
        ]
      },
      paymentStatus: '',
      paymentInfo: ''
    };
  },
  methods: {
    ...mapActions('account', ['getAccount']),
    ...mapMutations('transaction', ['setRecentTransactions']),
    cancel() {
      this.drawerBack();
    },
    async next() {
      if (this.isFormValid) {
        const loader = this.showLoader();
        try {
          if (
            this.getPayDetails.contactChange ||
            !this.getSelectedContactData ||
            !this.getSelectedContactData.wire ||
            this.getSelectedContactData.wire.domestic.accountNumber !==
              this.contactDetails.wire.domestic.accountNumber ||
            this.getSelectedContactData.wire.domestic.routingNumber !==
              this.contactDetails.wire.domestic.routingNumber ||
            this.getSelectedContactData.wire.domestic.accountType !==
              this.contactDetails.wire.domestic.accountType ||
            this.getSelectedContactData.wire.domestic.bankName !==
              this.contactDetails.wire.domestic.bankName
          ) {
            this.contactChange = true;
          }
          const data = await payToContact(
            'domestic',
            {
              accountId: this.getSelectedAccount.id,
              contactId: this.getSelectedContactData.id,
              amount: '' + this.formData.amount,
              description: this.formData.description,
              type: 'domestic'
            },
            this.contactChange
              ? {
                id: this.getSelectedContactData.id,

                ...this.contactDetails
              }
              : null
          );

          this.paymentInfo = data;
          this.paymentStatus = data.status;
          this.setRecentTransactions([]);
          this.drawerPush('pay-success');
          loader.close();
        } catch (e) {
          this.apiErrorMessage(e);
          loader.close();
        }
      }
    },
    done() {
      this.goToDashboard();
    }
  }
};
</script>
